besnr-highlight {
	-webkit-transition: background-color 0.3s ease;
	transition: background-color 0.3s ease;
	background-color: #5accf2;
	padding: 3px;
}

.besnr-form-ui .dashicons {
	font-size: 16px;
	width: auto;
	height: auto;
	margin: 0 2px;
	top: 1px;
	position: relative;
}

.besnr-form-ui input[type=text] {
	width: 100%;
}

.besnr-form-ui label span {
	position: relative;
	top: 0px;
}

.besnr-form-ui .button-group {
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-webkit-align-items: center;
	-ms-flex-align: center;
	align-items: center;
}

.besnr-form-ui .button-group .components-button.is-primary,
.besnr-form-ui .button-group .button {
	border-radius: 0;
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-webkit-align-items: center;
	-ms-flex-align: center;
	align-items: center;
	-webkit-box-pack: center;
	-webkit-justify-content: center;
	-ms-flex-pack: center;
	justify-content: center;
	min-height: 30px;
}

.besnr-form-ui .button-group .components-button.is-primary:first-child,
.besnr-form-ui .button-group .components-button.is-primary:first-child,
.besnr-form-ui .button-group .button:first-child,
.besnr-form-ui .button-group .button:first-child {
	border-top-left-radius: 4px;
	border-bottom-left-radius: 4px;
	border-right: 1px solid rgba(34, 35, 37, 0.15);
}

.besnr-form-ui .button-group .components-button.is-primary:last-child,
.besnr-form-ui .button-group .components-button.is-primary:last-child,
.besnr-form-ui .button-group .button:last-child,
.besnr-form-ui .button-group .button:last-child {
	border-top-right-radius: 4px;
	border-bottom-right-radius: 4px;
	border-left: 1px solid rgba(34, 35, 37, 0.15);
}

.components-panel {
	display: -webkit-box;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-webkit-flex-direction: column;
	-ms-flex-direction: column;
	flex-direction: column;
}

.edit-post-sidebar .components-panel__body.block-editor-search-replace {
	-webkit-box-ordinal-group: 10000;
	-webkit-order: 9999;
	-ms-flex-order: 9999;
	order: 9999;
}

